












































































import uniSegmentedControl from '../../../components/uni-segmented-control.vue';
import uniPopup from '../../../components/uni-popup.vue';
export default {
	data() {
		return {
			items: [
				'信用卡',
				'储蓄卡',
			],
			current: 0,
			activeColor: '#fe5950',
			styleType: 'text',
			creditCard: [],
			bankCard: [],
			Loop:'',
			popType: 'middle',
			tiptitle: '提示',
			showPopupMiddle: false,
			tipcontent:'',
			is_validate:'',
		}
	},
	onLoad() {
		//uni.startPullDownRefresh();
	},
	onShow(){
		this.getCardData();
	},
	onPullDownRefresh() {
		clearTimeout(this.Loop);
		this.getCardData();
		uni.stopPullDownRefresh();
	},
	onShareAppMessage() {
		return {
			title: this.webshareTitle,
			path: '/pages/tabBar/index/index'
		}
	},
	onNavigationBarButtonTap(e) {
		const openid = this.$store.state.openid;
		if(openid === null){
			uni.navigateTo({
				url: '/pages/user/login/login'
			})
			return false;
		}
		
		if(this.is_validate!=1){
			uni.showModal({ //提醒用户更新
				title: '提示',
				content: '您还没有进行实名认证，是否立即前往认证？',
				success: (res) => {
					if (res.confirm) {
						uni.navigateTo({
							url: '/pages/user/info/realname'
						})
					}
				}
			})
			return false;
		}
		if(this.current==0){
			this.onAddCredit();
		}else if(this.current==1){
			uni.navigateTo({
				url: '/pages/card/bank/add'
			})
		}
	},
	components: {
		uniSegmentedControl,uniPopup
	},
	methods: {
		//统一的关闭popup方法
		hidePopup: function() {
			this.showPopupMiddle = false;
			uni.navigateTo({
				url: '/pages/card/credit/add'
			})
		},
		//展示居中 popup
		showMiddlePopup: function() {
			//this.hidePopup();
			this.popType = 'middle';
			this.showPopupMiddle = true;
		},
		onAddCredit(){
			const openid = this.$store.state.openid;
			if(openid === null){
				uni.navigateTo({
					url: '/pages/user/login/login'
				})
				return false;
			}
			if(this.is_validate!=1){
				uni.showModal({ //提醒用户更新
					title: '提示',
					content: '您还没有进行实名认证，是否立即前往认证？',
					success: (res) => {
						if (res.confirm) {
							uni.navigateTo({
								url: '/pages/user/info/realname'
							})
						}
					}
				})
				return false;
			}
			if(this.bankCard.length==0){
				uni.showModal({
					content:'需要先绑定储蓄卡！',
					showCancel:true,
					success:function(e){	
						if(e.confirm){
							uni.navigateTo({
								url: '/pages/card/bank/add'
							})
						}
					}
				})
			}else{
				if(this.creditCard.length==0){
					this.tiptitle = '绑卡须知';
					this.showMiddlePopup();
				}else{
					uni.navigateTo({
						url: '/pages/card/credit/add'
					})
				}
			}
		},
		goDetailPage(url) {
			const openid = this.$store.state.openid;
			if(openid == null){
				uni.navigateTo({
					url: '/pages/user/login/login'
				})
				return false;
			}
			if(this.is_validate!=1){
				uni.showModal({ //提醒用户更新
					title: '提示',
					content: '您还没有进行实名认证，是否立即前往认证？',
					success: (res) => {
						if (res.confirm) {
							uni.navigateTo({
								url: '/pages/user/info/realname'
							})
						}
					}
				})
				return false;
			}
			uni.navigateTo({
				url: url
			})
		}, 
		onClickItem(index) {
			if (this.current !== index) {
				this.current = index;
			}
		},
		getCardData(){
			const openid = this.$store.state.openid;
			const sessionKey = this.$store.state.sessionKey;
			if(openid == null){
				return;
			}
			try {
				this.$http.post(this.websiteUrl+'/api/api/getCardData',{openid,sessionKey},(res) => {
					if(res.data.code==1){
						this.bankCard = res.data.result.bankCard;
						this.creditCard = res.data.result.creditCard;
						this.is_validate = res.data.result.is_validate;
						this.tipcontent = res.data.result.article.content.replace(/\\/g, "").replace(/<img/g, "<img style=\"display:none;\"");
					}else{
						uni.showModal({
							content:res.data.msg,
							showCancel:false
						})
					}
				});
			} catch (e) {
				uni.showModal({
					content:'网络异常,请稍后重试...',
					showCancel:false
				})
			}
		},
		delCredit(credit_id){
			clearTimeout(this.Loop); //再次清空定时器，防止重复注册定时器
			const that =this;
			this.Loop = setTimeout(function() {
				uni.showModal({
					content: "是否删除该信用卡？",
					confirmText: "确定",
					cancelText: "取消",
					success:function(e){
						if(e.confirm){
							clearTimeout(that.Loop);
							const openid = that.$store.state.openid;
							const sessionKey = that.$store.state.sessionKey;
							try {
								that.$http.post(that.websiteUrl+'/api/card/delCredit',{openid,sessionKey,credit_id},(res) => {
									if(res.data.code==1){
										that.getCardData();
									}else{
										uni.showModal({
											content:res.data.msg,
											showCancel:false
										})
									}
								});
							} catch (e) {
								uni.showModal({
									content:'网络异常,请稍后重试...',
									showCancel:false
								})
							}
						}
					}
				})
			}.bind(this), 2000);
		},
		delCreditAct(credit_id){
			
		},
		delBank(bankcard_id){
			//clearTimeout(this.Loop); //再次清空定时器，防止重复注册定时器
			const that =this;
			//this.Loop = setTimeout(function() {
				uni.showModal({
					content: "是否删除该储蓄卡？",
					confirmText: "确定",
					cancelText: "取消",
					success:function(e){
						if(e.confirm){
							//clearTimeout(that.Loop);
							const openid = that.$store.state.openid;
							const sessionKey = that.$store.state.sessionKey;
							try {
								that.$http.post(that.websiteUrl+'/api/card/delBank',{openid,sessionKey,bankcard_id},(res) => {
									if(res.data.code==1){
										that.getCardData();
									}else{
										uni.showModal({
											content:res.data.msg,
											showCancel:false
										})
									}
								});
							} catch (e) {
								uni.showModal({
									content:'网络异常,请稍后重试...',
									showCancel:false
								})
							}
						}
					}
				})
			//}.bind(this), 2000);
		},
		cleartime(credit_id) {
			// 这个方法主要是用来将每次手指移出之后将计时器清零
			clearTimeout(this.Loop);
		},
		goTorepay(credit_id){
			uni.navigateTo({
				url: '/pages/card/credit/info?credit_id='+credit_id
			})
		}
	}
}
